import * as yup from "yup";

export const signupUserSchemaRegister = yup.object().shape({
         taiKhoan: yup.string().required("Ti khon khng c  trng!"),
         matKhau: yup
           .string()
           .required("Mt khu khng c  trng!")
           .matches(/^[a-zA-Z0-9]{6,10}$/, "Mt Khu phi t 6 n 10 k t"),
         hoTen: yup.string().required("H tn khng c  trng!"),
         email: yup
           .string()
           .required("Email khng c  trng!")
           .email("Email khng hp l!"),
         soDT: yup
           .string()
           .required("S in thoi khng c  trng!")
           .matches(
             /((09|03|07|08|05)+([0-9]{8})\b)/g,
             "S in thoi khng hp l "
           ),
       });


export const signinUserSchemaLogin = yup.object().shape({
  taiKhoan: yup.string().required("Ti khon khng c  trng!"),
  matKhau: yup
    .string()
    .required("Mt khu khng c  trng!")
    .matches(/^[a-zA-Z0-9]{6,10}$/, "Mt Khu phi t 6 n 10 k t"),
});
     

